import React from 'react';
import { Route, BrowserRouter, Switch } from "react-router-dom";

import Main from './Components/Mobile/Main.js';
import Guide from './Components/Mobile/Guide.js';
import Camera from './Components/Mobile/Camera.js';

function Router() {
    return (
        <BrowserRouter basename={process.env.PUBLIC_URL}>
            <div>
                <Switch>
                    <Route path="/" exact={true} component={Main} />
                    <Route path="/Guide" exact={true} component={Guide} />
                    <Route path="/Camera" exact={true} component={Camera} />
                </Switch>
            </div>
        </BrowserRouter>
    );
}

export default Router;